<template>
  <div class="v-toast">
    <c-btn type="primary" @click="alertClick">alert</c-btn>
    <c-btn type="primary" @click="confirmClick">confirm</c-btn>
    <p> confirm</p>
    <c-btn type="primary" style="margin-top: 1000px;" @click="confirmClick"> confirm </c-btn>
  </div>
</template>

<script>
export default {
  methods: {
    alertClick () {
      this.$Confirm({
        text: ' alert',
        ok: {
          click: () => {
            window.alert('ok')
          }
        }
      })
    },
    confirmClick () {
      this.$Confirm({
        text: ' confirm',
        ok: {
          text: '',
          click: () => {
            window.alert('ok')
          }
        },
        cancel: {
          text: '',
          click: () => {
            window.alert('cancel')
          }
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import './style';
</style>



